button {
    border: none;
    background-color: #f37c7c;
    background-image: linear-gradient(315deg, #cd3c2b 0%, #1697c2 74%);
    font-size: 24px;
    color: rgb(255, 255, 255);
    font-weight: bold;
    cursor: pointer;
    border-radius: 10px;
    outline: none;
  }
  
  button:hover {
    background-color: #dfe916;
    background-image: linear-gradient(315deg, #e9ec46 0%, #c4c720 74%);
  }
  
  .equals {
    grid-column: 3 / 5;
    background-color: rgb(42, 96, 220);
  }
  
  .equals:hover {
    background-color: #dfe916;
    background-image: linear-gradient(315deg, #d96015 0%, #ec4007 74%);
  }
  